<template>
    <div class="container-fluid">
        <div class="col-12 text-center py-3 my-5">
            <h1 class="theme-title my-0">Мои подписки</h1>
        </div>
        <div v-if="watchData.length > 0" class="d-flex row align-items-center justify-content-center">
            <AppCart v-for="watch in watchData" :key="watch.filmId" :state="'default'" :type="watch.type" :kpid="watch.filmId" :title="watch.nameRu" :year="watch.year" />
        </div>
        <div v-else class="row">
            <div class="col-12 col-lg-6 offset-lg-3 text-center theme-panel py-5 rounded">
                <h3 class="theme-title mb-5">Как-то здесь пусто…</h3>
                <a @click.prevent="$router.push('/search')" href="/search" class="btn btn-large btn-outline-primary btn-rounded">{{btn_text}}</a>
            </div>
        </div>
    </div>
</template>

<script>
import AppCart from '../components/AppCart.vue'
const axios = require('axios')
export default {
    name: 'Subscriptions',
    components: {
        AppCart
    },
    data: () => {
        return {
            prop: 'Subscriptions',
            watchData: [],
            btn_text: 'Загрузка подписок'
        }
    },
    methods: {
        
    },
    mounted() {
        document.title = 'Subscriptions'
        axios
            .get('https://iny.su/api.php?_action=media.getSubscribe&v=0.1&jwt=' + this.$store.getters.USER_TOKEN)
            .then(data => {
                this.watchData = data.data.subsData
            })
        setTimeout(() => {
            this.btn_text = 'Найти любимые сериалы'
        }, 3000);
    }
}
</script>